const IMAGES = [
  { name: 'Binz Beach', country: 'Germany', url: '/resources/images/background/binz-beach.jpg' },
  { name: 'Hallstatt', country: 'Austria', url: '/resources/images/background/hallstatt.jpg' },
  { name: 'King\'s Chair Beach', country: 'Germany', url: '/resources/images/background/kings-chair-beach.jpg' },
  { name: 'Lifeguards Nearby Pier Sellin', country: 'Germany', url: '/resources/images/background/lifeguards-nearby-pier-sellin.jpg' },
  { name: 'Pier Sellin', country: 'Germany', url: '/resources/images/background/pier-sellin.jpg' },
  { name: 'Sellin Dog Beach', country: 'Germany', url: '/resources/images/background/sellin-dog-beach.jpg' },
  { name: 'Sellin East Train Station', country: 'Germany', url: '/resources/images/background/sellin-east-train-station.jpg' }
]

export default class BackgroundImage {
  constructor() {
    this.imageIndex = this.randomIndex
  }

  get randomIndex() {
    return Math.floor(Math.random() * Math.floor(IMAGES.length))
  }

  get randomImage() {
    let index
    do {
      index = this.randomIndex
    } while (index === this.imageIndex && IMAGES.length > 1)
    this.imageIndex = index
    return IMAGES[index]
  }
}
