import i18n from 'i18n'
import { ArrayWatcher, Emitter } from 'shared/services'
import { Game } from 'shared/game'

const t = i18n.getFixedT()

export default class ThreeDartsOnePrizeGame extends Game {
  get winners() {
    const { gameScores, players, scoreboards } = this.state
    const winnerIndexes = []
    gameScores.forEach((score, index) => {
      if ((score > 65 || score < 21) &&
          scoreboards[index].rounds[0].scores.length === 3 &&
          scoreboards[index].rounds[0].scores.every(s => s.value > 0)) {
        winnerIndexes.push(index)
      }
    })
    return winnerIndexes.map(i => players[i])
  }

  onDartboardHit = score => {
    const {
      currentPlayerScoreboard: scoreboard,
      increaseCurrentPlayerGameScore,
      isLastRoundFinished
    } = this.state
    increaseCurrentPlayerGameScore(score.value)
    const round = scoreboard.getCurrentRound()
    if (isLastRoundFinished()) {
      this.handleGameOver()
    } else if (round && round.getCurrentScore().index === 2) {
      this.addGameMessage('nextPlayer')
    }
  }

  onChangeButtonClick = () => {
    const { addRoundForCurrentPlayer, clearMessages } = this.state
    addRoundForCurrentPlayer()
    clearMessages()
    this.addGameMessage('next')
  }

  handleGameOver = () => {
    if (this.state.messages.length === 0) {
      this.addGameResultMessage()
    } else {
      const arrayWatcher = new ArrayWatcher(this.state.messages)
      arrayWatcher.watch(messages => {
        if (messages.length === 0) {
          this.addGameResultMessage()
          arrayWatcher.stop()
        }
      })
    }
    this.pause()
    Emitter.off('CHANGE_BUTTON_CLICK', this.handleChangeButtonClick)
    this.onGameOver()
  }

  addGameResultMessage = () => {
    const winners = this.winners.map(w => w.name)
    const names = winners.length === 1 ? winners[0] : winners.join(t(', '))
    winners.length ? this.addGameMessage('prizeForThreeDartsOnePrizeGame', names) : this.addGameMessage('fail')
  }
}
