export default class ArrayWatcher {
  timerId = ''

  constructor(array) {
    this.array = array
    this.prevArray = [...array]
  }

  watch = (listener, interval = 500) => {
    this.timerId = window.setInterval(() => {
      const isChanged = this.prevArray.length !== this.array.length
      if (isChanged) {
        this.prevArray = [...this.array]
        listener && typeof listener === 'function' && listener(this.array)
      }
    }, interval)
  }

  stop = () => window.clearInterval(this.timerId)
}
