import { useContext, useEffect, useState } from 'react'
import { useTranslation } from 'react-i18next'
import { makeStyles } from '@material-ui/core/styles'
import { Typography } from 'components'
import { BackgroundImageContext } from 'components/contexts'
import styles from './styles'

const useStyles = makeStyles(styles)

export default function OneBull({ message, onClose }) {
  const [open, setOpen] = useState(true)
  const { t } = useTranslation()
  const { backgroundImage } = useContext(BackgroundImageContext)
  const classes = useStyles({ backgroundImageUrl: backgroundImage.url })
  useEffect(() => {
    const timerId = setTimeout(() => {
      setOpen(false)
      onClose && onClose(message)
    }, message.autoHideDuration)
    return () => clearTimeout(timerId)
  }, [message, onClose, setOpen])
  if (open) {
    return (
      <div className={classes.root}>
        <Typography className={classes.text} variant='h1'>{t('One bull')}</Typography>
      </div>
    )
  } else {
    return null
  }
}
